svg {
  display: block;
  fill: #323441; }

*,
*::before,
*::after {
  box-sizing: border-box; }

html,
body {
  height: 100%; }

html {
  font-family: sans-serif;
  line-height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%; }

body {
  background-color: #ffffff;
  color: #323441;
  font-family: "Roboto", -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;
  font-size: 1rem;
  font-weight: 400;
  line-height: 100%;
  margin: 0; }

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0 !important; }

[hidden] {
  display: none !important; }

.page {
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 100%;
  position: relative; }
  .page__header {
    flex: 0 0 auto;
    position: fixed;
    transition: background .25s ease;
    top: 0;
    width: 100%;
    z-index: 3; }
  @media (min-width: 768px) {
    .mfp-bg.mfp-fade.mfp-ready ~ .page .page__header {
      padding-right: 17px; } }
  .page__main {
    flex: 1 0 auto;
    overflow: hidden; }
  .page__footer {
    bottom: 0;
    flex: 0 0 auto;
    width: 100%; }
  .page__nav {
    background: #000;
    height: 100%;
    left: 0;
    position: fixed;
    top: 0;
    width: 0;
    z-index: 5; }
    @media (min-width: 768px) {
      .page__nav {
        display: none; } }
  .page__darker {
    background: #000;
    display: block;
    height: 100%;
    left: 0;
    opacity: 0;
    position: fixed;
    top: 0;
    transition: all .25s;
    visibility: hidden;
    width: 100%;
    z-index: 3; }
    .nav-open .page__darker {
      opacity: .16;
      visibility: visible; }
    @media (min-width: 768px) {
      .page__darker {
        display: none; } }

